body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.success {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  background: lightgreen;
  overflow: hidden;
  opacity: 0.75;
  text-align: center;
  line-height: 2.5;
}

.failure {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  background: red;
  overflow: hidden;
  opacity: 0.75;
  text-align: center;
  line-height: 2.5;
}